<template>
  <div>
    <div class="my-5 row justify-content-center">
      <validation-observer v-slot="{ invalid }" class="col-md-6">
        <form @submit.prevent="OrderPay">
          <validation-provider
            rules="required|email"
            v-slot="{ errors, classes }"
          >
            <div class="form-group">
              <label for="useremail">Email</label>
              <input
                type="email"
                class="form-control"
                name="email"
                id="useremail"
                v-model="form.user.email"
                placeholder="請輸入 Email"
                :class="classes"
              />
              <span class="invalid-feedback">{{ errors[0] }}</span>
            </div>
          </validation-provider>

          <validation-provider rules="required" v-slot="{ errors, classes }">
            <div class="form-group">
              <label for="username">收件人姓名</label>
              <input
                type="text"
                class="form-control"
                name="name"
                id="username"
                v-model="form.user.name"
                placeholder="輸入姓名"
                :class="classes"
              />
              <span class="invalid-feedback">姓名欄位不可留空</span>
            </div>
          </validation-provider>

          <div class="form-group">
            <label for="usertel">收件人電話</label>
            <input
              type="tel"
              class="form-control"
              id="usertel"
              v-model="form.user.tel"
              placeholder="請輸入電話"
            />
          </div>

          <validation-provider rules="required" v-slot="{ errors, classes }">
            <div class="form-group">
              <label for="useraddress">收件人地址</label>
              <input
                type="text"
                class="form-control"
                name="address"
                id="useraddress"
                v-model="form.user.address"
                placeholder="請輸入地址"
                :class="classes"
              />
              <span class="invalid-feedback">地址欄位不得留空</span>
            </div>
          </validation-provider>

          <div class="form-group">
            <label for="comment">留言</label>
            <textarea
              name=""
              id="comment"
              class="form-control"
              cols="30"
              rows="10"
              v-model="form.message"
            ></textarea>
          </div>
          <div class="send d-flex justify-content-between text-right">
              <button class="btn btn-primary" @click="prev">上一步</button>
            <button class="btn btn-danger" :disabled="invalid">送出訂單</button>
            
          </div>
        </form>
      </validation-observer>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  data() {
    return {
      products: [],
      product: {},
      cart: {},

      isLoading: false,
      status: {
        loadingItem: ""
      },
      form: {
        user: {
          address: "",
          email: "",
          tel: "",
          name: ""
        },
        message: ""
      },
      currentActive : this.currentValue,
    };
  },
  props: {
    currentValue: Number,
    required: true
  },
  methods: {
    getProducts() {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        console.log(response);
        vm.isLoading = false;
        vm.products = response.data.products;
      });
    },
    openProductModal(id) {
      // /api/:api_path/product/:id
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(url).then(response => {
        console.log(response.data);
        vm.isLoading = false;
        vm.product = response.data.product;
        $("#productModal").modal("show");
      });
    },
    addToCart(id, qty = 1) {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      const vm = this;
      const cart = {
        product_id: id,
        qty
      };
      this.$http.post(url, { data: cart }).then(response => {
        console.log(response);
        vm.status.loadingItem = "";
        this.getCart();
        $("#productModal").modal("hide");
      });
    },
    getCart() {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      const vm = this;
      this.$http.get(url).then(response => {
        console.log(response.data.data);
        vm.cart = response.data.data;
      });
    },
    deleteItemInChart(id) {
      ///api/:api_path/cart/:id
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.delete(url).then(response => {
        this.getCart();
        console.log(response.data);
        vm.isLoading = false;
        // vm.cart = response.data.data;
      });
    },
    prev(){
        this.$emit("prevMethod");
    },
    OrderPay() {
      this.$emit("nextMethod");
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
      const vm = this;
      const order = vm.form;
      // console.log(order);
      // vm.isLoading = true;
      this.$http.post(url, { data: order }).then(response => {
        if (response.data.success) {
          vm.$router.push(`/customer/customer_checkout/${response.data.orderId}`);
          console.log("訂單已成功建立", response);
        } else {
          console.log("訂單有問題", response);
        }
      });
    }
  },
  created() {
    this.getProducts();
    this.getCart();
    // this.$bus.$emit('message:push', '這裡是一段訊息', 'success');
  }
};
</script>

<style lang="scss" scoped>
.send {
  position: relative;
//   left: 60px;
margin-bottom: 30px;
  top: 86px;
}
</style>



