<template>
  <div class=" column justify-content-center">
    <div class="bar">
      <div class="progress-container">
        <div class="progress" id="progress" :style="{ width: width }"></div>
        <ul>
          <li>
            <img
              src="/static/images/shopping-cart-empty-side-view.svg"
              alt=""
              srcset=""
              class="symbol"
            />
            <div class="circle active">
              1
            </div>
            <p>Cart</p>
          </li>

          <li>
            <img src="/static/images/form.svg" alt="" srcset="" /
            class="symbol">
            <div class="circle" :class="{ active: currentActive >= 2 }">
              2
            </div>
            <p>Guest</p>
          </li>
          <li>
            <img src="/static/images/money.svg" alt="" / class="symbol">
            <div class="circle" :class="{ active: currentActive === 3 }">
              3
            </div>
            <p>Pay</p>
          </li>
        </ul>
      </div>
    </div>

    <div class="container-fluid">
      <main role="main">
        <router-view
          v-on:nextMethod="next"
          v-on:prevMethod="prev"
          :currentValue="currentActive"
        ></router-view>
      </main>
    </div>
  </div>
</template>

<style lang="scss">
@import url("https://fonts.googleapis.com/css2?family=Abel&display=swap");

:root {
  --line-border-fill: #3498db;
  --line-border-empty: #e0e0e0;
}

.bar {
  margin-top: 100px;
  display: flex;
  justify-content: center;
}

.progress-container {
  display: flex;
  justify-content: space-between;
  position: relative;
  margin-bottom: 30px;
  max-width: 100%;
  width: 350px;
}

// .btn-next , .btn-prev{
//   background: red;
//   height: 30px;
//   width: 100px;
//   border-radius: 10px;
// }

.symbol {
  position: relative;
  left: -5px;
  height: 40px;
  width: 40px;
}

.progress-container::before {
  content: ""; /* must be added when using the ::before pseudoselector */
  background-color: var(--line-border-empty);
  position: absolute;
  top: 50%;
  // left: 12px;
  transform: translateY(-50%);
  height: 4px;
  width: 100%;
  z-index: -1;
}

.progress-container ul {
  left: 3px;
  width: 100%;
  justify-content: space-between;
  list-style: none;
  display: flex;
  padding: 0;
  position: relative;
  top: 10px;
  li {
    display: flex;
    flex-direction: column;
  }
}

.progress {
  background-color: var(--line-border-fill);
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(
    -50%
  ); /* centers the line vertically along the y-axis */
  height: 4px;
  width: 0%;
  z-index: -1;
  transition: 0.4s ease;
}

.circle {
  position: relative;
  // right: -1px;
  background-color: #fff;
  color: #999;
  border-radius: 50%;
  height: 30px;
  width: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 3px solid var(--line-border-empty);
  transition: 0.4s ease;
}

.circle.active {
  border-color: var(--line-border-fill);
}
</style>

<script>
import Cart from "./Cart";
import Form from "./Form";
export default {
  data() {
    return {
      currentActive: 1,
      steps: 1,
      process: 3,
      width: 0.01
    };
  },
  components: {
    Cart,
    Form
    // Alert,
  },
  methods: {
    next() {
      const vm = this;
      vm.currentActive++;

      if (vm.currentActive > vm.process) {
        vm.currentActive = vm.process;
      }
      vm.width = ((vm.currentActive - 1) / (vm.process - 1)) * 100 + "%";

      this.msg = vm.currentActive;

      if (this.$route.path === "/customer/cart") {
        vm.$router.push("/customer/form");
      }
      // console.log(this.$route.path);
    },

    prev() {
      const vm = this;

      vm.currentActive--;
      vm.width = ((vm.currentActive - 1) / (vm.process - 1)) * 100 + "%";
      // console.log(vm.currentActive);
      // console.log(vm.width);

      if (this.$route.path === "/customer/form") {
        vm.$router.push("/customer/cart");
      }
    }
  },
  mounted() {
    this.circles = document.querySelectorAll(".circle");
    this.actives = document.querySelectorAll(".active");
  },
  created() {
    const token = document.cookie.replace(
      /(?:(?:^|.*;\s*)hexToken\s*=\s*([^;]*).*$)|^.*$/,
      "$1"
    );

    this.$http.defaults.headers.common.Authorization = token;
  }
};
</script>



