<template>
  <div>
    <div class="d-flex  justify-content-center">
      <div class="my-5 row  justify-content-center">
        <div class="my-5 row justify-content-center">
          <table class="table">
            <thead>
              <th></th>
              <th>品名</th>
              <th>數量</th>
              <th>單價</th>
            </thead>
            <tbody>
              <tr v-for="item in cart.carts">
                <td class="align-middle">
                  <button
                    type="button"
                    class="btn btn-outline-danger btn-sm"
                    @click="deleteItemInChart(item.id)"
                  >
                    <i class="far fa-trash-alt"></i>
                  </button>
                </td>
                <td class="align-middle">
                  {{ item.product.title }}
                  <div class="text-success" v-if="item.coupon">
                    已套用優惠券
                  </div>
                </td>
                <td class="align-middle">
                  {{ item.qty }}/{{ item.product.unit }}
                </td>
                <td class="align-middle text-right">{{ item.final_total }}</td>
              </tr>
            </tbody>
            <tfoot>
              <tr>
                <td colspan="3" class="text-right">總計</td>
                <td class="text-right">{{ cart.total }}</td>
              </tr>
              <tr v-if="cart.final_total !== cart.total">
                <td colspan="3" class="text-right text-success">折扣價</td>
                <td class="text-right text-success">{{ cart.final_total }}</td>
              </tr>
            </tfoot>
          </table>
          <div class="input-group mb-3 input-group-sm">
            <input
              type="text"
              class="form-control"
              placeholder="請輸入優惠碼"
              v-model="coupon_code" 
            />
            <div class="input-group-append">
              <button class="btn btn-outline-secondary" type="button" @click="addCouponCode">
                套用優惠碼
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="button d-flex justify-content-between">
      <button
        type="button"
        class="btn btn-outline-secondary"
        id="prev"
        @click="prev()"
        :disabled="currentActive == 1"
      >
        上一步
      </button>
      <button
        type="button"
        class="btn btn-outline-success"
        id="next"
        @click="next()"
        :disabled="currentActive == 3"
        v-if="this.$route.path != '/customer/form'"
      >
        下一步
      </button>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.button {
      position: relative;
    width: 525px;
    left: 670px;
}
</style>

<script>
import $ from "jquery";

export default {
  data() {
    return {
      products: [],
      product: {},
      cart: {},
      step: 0,
      isLoading: false,
      status: {
        loadingItem: ""
      },
      form: {
        user: {
          address: "",
          email: "",
          tel: "",
          name: ""
        },
        message: ""
      },
      currentActive: this.currentValue,
      coupon_code: '',
    };
  },
  props: {
    currentValue: Number,
    required: true
  },
  methods: {
    getProducts() {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        console.log(response);
        vm.isLoading = false;
        vm.products = response.data.products;
      });
    },
    openProductModal(id) {
      // /api/:api_path/product/:id
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(url).then(response => {
        console.log(response.data);
        vm.isLoading = false;
        vm.product = response.data.product;
        $("#productModal").modal("show");
      });
    },
    addToCart(id, qty = 1) {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      const vm = this;
      const cart = {
        product_id: id,
        qty
      };
      this.$http.post(url, { data: cart }).then(response => {
        console.log(response);
        vm.status.loadingItem = "";
        this.getCart();
        $("#productModal").modal("hide");
      });
    },
    next() {
      const vm = this ;
        if(vm.cart.carts.length < 1){
          alert("1234");
          return;
      }
      this.$emit("nextMethod");
    //   console.log(vm.cart.carts.length);
    
    },
    prev() {
      this.$emit("prevMethod");
    },
    getCart() {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      const vm = this;
      this.$http.get(url).then(response => {
        console.log(response.data.data);
        vm.cart = response.data.data;
      });
    },
    deleteItemInChart(id) {
      ///api/:api_path/cart/:id
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.delete(url).then(response => {
        this.getCart();
        console.log(response.data);
        vm.isLoading = false;
        // vm.cart = response.data.data;
      });
    },
    OrderPay() {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
      const vm = this;
      const order = vm.form;
      // console.log(order);
      // vm.isLoading = true;
      this.$http.post(url, { data: order }).then(response => {
        if (response.data.success) {
          vm.$router.push(`customer_checkout/${response.data.orderId}`);
          console.log("訂單已成功建立", response);
        } else {
          console.log("訂單有問題", response);
        }
      });
    },
    addCouponCode(){
        const vm = this;
        const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
        const coupon = {
            code: vm.coupon_code
        };
        vm.isLoading = true;
        this.$http.post(url, {data : coupon}).then(response => {
        this.getCart();
        console.log(response.data);
        vm.isLoading = false;
        // vm.cart = response.data.data;
      });
    },
  },
  created() {
    this.getProducts();
    this.getCart();
    // this.$bus.$emit('message:push', '這裡是一段訊息', 'success');
  }
};
</script>



