  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="BMW_Background">
      <div class="banner_Lambo d-flex justify-content-center align-items-center">
        <div class="container mb-5 mt-5 pb-3">
          <!-- <div class="card-bg"></div> -->
          <div
            id="carouselExampleFade"
            class="carousel slide carousel-fade mt-3  mx-3"
            data-ride="carousel"
          >
            <div class="carousel-inner">
              <div class="carousel-item active" data-interval="3000">
                <img
                  src="https://imgur.com/dCTFzXT.jpeg"
                  class="d-block w-100 img_Lambo"
                  alt="..."
                />
                <div class="carousel-caption d-none d-md-block">
                  <h1>Lamborghini Huracan EVO Coupe</h1>
                  <h3>Meaningful, Memorable, Likable</h3>
                </div>
              </div>
              <div class="carousel-item" data-interval="3000">
                <img
                  src="https://imgur.com/gEmyrd6.jpg"
                  class="d-block w-100 img_Lambo"
                  alt="..."
                />
                <div class="carousel-caption d-none d-md-block ">
                  <h1>Lamborghini Aventador Roadster SVJ Xago</h1>
                </div>
              </div>
              <div class="carousel-item" data-interval="3000">
                <img
                  src="https://imgur.com/s88ZAd6.jpg"
                  class="d-block w-100 img_Lambo"
                  alt="..."
                />
                <div class="carousel-caption d-none d-md-block">
                  <h1>Lamborghini Aventador Superveloce Coupe</h1>
                </div>
              </div>
            </div>
            <a
              class="carousel-control-prev"
              href="#carouselExampleFade"
              role="button"
              data-slide="prev"
            >
              <span
                class="carousel-control-prev-icon"
                aria-hidden="true"
              ></span>
              <span class="sr-only">Previous</span>
            </a>
            <a
              class="carousel-control-next"
              href="#carouselExampleFade"
              role="button"
              data-slide="next"
            >
              <span
                class="carousel-control-next-icon"
                aria-hidden="true"
              ></span>
              <span class="sr-only">Next</span>
            </a>
          </div>
          -->
        </div>
      </div>
      <div class="content">
        <div class=" d-flex col-md-12 d-flex justify-content-center">
          <div class="row mt-4 pt-5 d-flex justify-content-left">
            <div
              class="col-md-3 mb-3 "
              v-for="item in products"
              :key="item.id"
              v-if="item.category == 'Lamborghini'"
            >
              <div class="card  bg-dark text-light">
                <div
                  style="height: 150px; background-size: cover; background-position: center"
                  :style="{ backgroundImage: `url(${item.imageUrl})` }"
                ></div>
                <div class="card-body">
                  <span class="badge badge-secondary float-right ml-2 "
                    ><img src="/static/images/Lamborghini.ico" alt="" class="icon_Lambo"
                  /></span>
                  <h5 class="card-title">
                    <a href="#" class="text-primary ">{{ item.title }}</a>
                  </h5>
                  <p class="card-text">{{ item.description }}</p>
                  <div
                    class="d-flex justify-content-between align-items-baseline"
                  >
                    <div class="h5" v-if="!item.price">
                      {{ item.origin_price }} 元
                    </div>
                    <div class="h5" v-if="item.price">{{ item.price }} 元</div>
                  </div>
                </div>
                <div class="card-footer d-flex">
                  <button
                    type="button"
                    class="btn btn-outline-secondary btn-sm"
                    @click="openProductModal(item.id)"
                  >
                    <i class="fas fa-spinner fa-spin"></i>
                    查看更多
                  </button>
                  <button
                    type="button"
                    class="btn btn-outline-danger btn-sm ml-auto"
                    @click="addToCart(item.id)"
                  >
                    <i class="fas fa-spinner fa-spin"></i>
                    加到購物車
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="footer pt-5 pb-5 mx-auto d-flex justify-content-center ">
        <div class="container row">
          <h1 class="col-md-4 slogan">極速·尊榮·奢華</h1>
          <div class="input-group offset-md-7 col-md-5">
            <h5>訂閱我們的電子報，可以獲得最新車訊以及秘密折扣！</h5><br>
            <input
              type="mail"
              placeholder="abcdef456@gmail.com"
              class="form-control"
            />
            <div class="input-group-append">
              <button class="btn btn-light">訂閱</button>
            </div>
          </div>
        </div>
      </div>

      <div
        class="modal fade"
        id="productModal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title" id="exampleModalLabel">
                {{ product.title }}
              </h5>
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <img :src="product.imageUrl" class="img-fluid" alt="" />
              <blockquote class="blockquote mt-3">
                <p class="mb-0">{{ product.content }}</p>
                <footer class="blockquote-footer text-right">
                  {{ product.description }}
                </footer>
              </blockquote>
              <div class="d-flex justify-content-between align-items-baseline">
                <div class="h4" v-if="!product.price">
                  {{ product.origin_price }} 元
                </div>
                <del class="h6" v-if="product.price"
                  >原價 {{ product.origin_price }} 元</del
                >
                <div class="h4" v-if="product.price">
                  現在只要 {{ product.price }} 元
                </div>
              </div>
              <select name="" class="form-control mt-3" v-model="product.num">
                <option :value="num" v-for="num in 10" :key="num">
                  選購 {{ num }} {{ product.unit }}
                </option>
              </select>
            </div>
            <div class="modal-footer">
              <div class="text-muted text-nowrap mr-3">
                小計 <strong>{{ product.num * product.price }}</strong> 元
              </div>
              <button
                type="button"
                class="btn btn-primary"
                @click="addToCart(product.id, product.num)"
              >
                <i
                  class="fas fa-spinner fa-spin"
                  v-if="product.id === status.loadingItem"
                ></i>
                加到購物車
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="scss">
@import url("https://fonts.googleapis.com/css2?family=Zhi+Mang+Xing&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lobster&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Kalam:wght@300&display=swap");

.img_Lambo {
 height: 100%;
  width: 100%;
}

.far {
  font-size: 40px;
}

.content {
  z-index: 10;
  position: relative;
  background: #3d3d5c;
}

.carousel-caption {
  font-family: "Lobster", cursive;
  justify-content: start;
  text-align: left;
  h3 {
    font-family: "Kalam", cursive;
  }
}

.carousel-control-prev,
.carousel-control-next {
  width: 6%;
}

.container {
  height: 600px;

  overflow: hidden;

  background-color: rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(100%);
}

.card {
  &:hover {
    box-shadow: 0px 0px 25px whitesmoke;
  }
}



.carousel {
  overflow: hidden;
  height: 100%;
}

.input-group-append {
  display: block;
}

.footer {
  // letter-spacing: 6px;
  font-family: "Sawarabi Mincho", sans-serif;
  color: #fff;
  position: relative;
  z-index: 20;
  background: #262673;
}

.icon_Lambo {
  height: 30px;
  width: 30px;
}

.banner_Lambo {
  background: url("https://imgur.com/BOfv8B9.jpg");
  z-index: 20;
  position: relative;
  width: 100vw;
  background-size: cover;
  width: 100%;
  overflow: hidden;
}
</style>

<script>
import $ from "jquery";
export default {
  data() {
    return {
      products: [],
      product: {},
      cart: {},
      pagination: {},
      isLoading: false,
      status: {
        loadingItem: ""
      }
    };
  },
  methods: {
    getProducts() {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products/all`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        console.log(response);
        vm.isLoading = false;
        vm.products = response.data.products;
        vm.pagination = response.data.pagination;
      });
    },
    openProductModal(id) {
      // /api/:api_path/product/:id
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(url).then(response => {
        console.log(response.data);
        vm.isLoading = false;
        vm.product = response.data.product;
        $("#productModal").modal("show");
      });
    },
    addToCart(id, qty = 1) {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      const vm = this;
      const cart = {
        product_id: id,
        qty
      };
      this.$http.post(url, { data: cart }).then(response => {
        console.log(response);
        vm.status.loadingItem = "";
        this.getCart();
        vm.$bus.$emit("getCart");
        $("#productModal").modal("hide");
      });
    },
    getCart() {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      const vm = this;
      this.$http.get(url).then(response => {
        console.log(response.data.data);
        vm.cart = response.data.data;
      });
    }
  },
  created() {
    this.getProducts();
  }
};
</script>



